@keyframes slideIn {
  0% {
    transform: translateX(-10px);
  }
  50% {
    transform: translateX(10px);
  }
  100% {
    transform: translateX(0px);
  }
}

.flash {
  background: #fff;
  box-shadow: 0 3px 10px rgba(0,0,0,0.15);
  margin-bottom: 1.5rem;
  padding: 1rem;
  position: relative;
  z-index: 1;
  display: flex;
  animation: slideIn forwards .1s ease-in-out 2;
  animation-timing-function: cubic-bezier(0.01, 1.68, 0.58, 1);
  & + .flash {
    animation-delay: 0.55s;
    & + .flash {
      animation-delay: 0.6s;
    }
  }
  &__text {
    flex: 1;
  }
  &__remove {
    background: none;
    color: $grey;
    border:0;
    &:hover {
      color: $darkBlue;
    }
  }
  &:after {
    content: '';
    display: block;
    background: white;
    position: absolute;
    width: calc(100% - 16px);
    height: calc(100% - 16px);
    top: 8px;
    left: 8px;
    z-index: -1;
    box-shadow: 0 0 2px 2px rgba(0,0,0,0.1);
  }
  &--success {
    background: $darkGreen;
  }
  &--error {
    background: $magenta;
  }
  &--info {
    background: $darkBlue;
  }
  p {
    margin: 0;
  }
}



